.App {
  padding: 1rem;
}

.App,
.signed-in-page {
  display: flex;
  flex-direction: column;
}

.project-container {
  display: flex;
  flex-direction: column;
}

.project-container header div section {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  /* width: 100%; */
  padding: 10px;
  box-shadow: 5px 10px 18px #888888;
  margin: 1rem 0;
}

.issue {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  border: 1px solid;
  padding: 10px;
  box-shadow: 5px 10px 18px #888888;
  margin: 1rem 0;
}

.issueName {
  max-width: 170px;
}

.actualTime {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.actualTime label {
  text-align: center;
}

.actualInput {
  width: 30px;
  height: 30px;
  text-align: center;
}

.saveActBtn {
  padding: 0.6rem;
}

table {
  text-align: center;
}

.logOutBtn {
  background-color: lightblue;
  color: black;
  font-weight: 700;
  width: fit-content;
  padding: 0.6rem;

}

button {
  cursor: pointer;
  border-radius: 5px;
}

form {
  width: 300px;
}

span {
  font-size: 25px;
}

.saveTimeBtn {
  padding: 0.4rem;
}